import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import AdmissionSubjectCriteriaDetailCardMenu from "./AdmissionSubjectCriteriaDetailCardMenu";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const AdmissionSubjectCriteriaDetailCard = ({ subCri, onRefetch, sgid }) => {
  const getQuery = useLocation();
  const params = useParams();
  const [open, setOpen] = useState(false);
  const onToggle = (e) => {
    e.preventDefault();
    setOpen((pre) => !pre);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/admission?type=criteria-subdetail`}
        state={{
          ...getQuery.state,
          // headId: subCri,
          nestedSubCri: subCri,
        }}
      >
        <div className={style.smc_card}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner}>
              <div className={style.smc_card_contatiner_inner}>
                <h6>{subCri?.group_name}</h6>
              </div>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          />
        </div>
      </Link>
      {open && (
        <AdmissionSubjectCriteriaDetailCardMenu
          onClose={() => setOpen((pre) => !pre)}
          sgid={sgid}
          menuData={subCri}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AdmissionSubjectCriteriaDetailCard;
