import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useAdmissionRemoveSubjectGroup } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionSubjectCriteriaDetailCardMenu = ({
  sgid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [admissionRemoveSubjectGroup] = useAdmissionRemoveSubjectGroup();

  const onDelete = () => {
    if (menuData?._id && sgid) {
      setDisabled((pre) => !pre);
      admissionRemoveSubjectGroup({
        sgid: sgid,
        bodyParameter: {
          ssid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default AdmissionSubjectCriteriaDetailCardMenu;
