import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useAdmissionSubGroupList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import AdmissionSubjectCriteriaDetailCard from "./AdmissionSubjectCriteriaDetailCard";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useLocation } from "react-router-dom";
import AddAdmissionSubjectCriteriaDetail from "./AddAdmissionSubjectCriteriaDetail";

const AdmissionSubjectCriteriaDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionSubGroupList,
    admissionSubGroupListLoading,
    admissionSubGroupListRefetch,
  } = useAdmissionSubGroupList({
    data: {
      sgid: getQuery.state?.subCri?._id,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.subCri?._id,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionSubGroupList?.list?.length) {
      let obj = nextPage(admissionSubGroupList?.list?.length);
      setState(() => obj);
    }
  }, [admissionSubGroupList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    admissionSubGroupListRefetch();
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.subCri?.subject_group_name ?? ""}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {admissionSubGroupList?.list?.map((subCri, index) =>
            admissionSubGroupList?.list?.length === index + 1 ? (
              <div key={subCri?._id} ref={ref}>
                <AdmissionSubjectCriteriaDetailCard
                  subCri={subCri}
                  onRefetch={onRefetch}
                  sgid={getQuery.state?.subCri?._id}
                />
              </div>
            ) : (
              <AdmissionSubjectCriteriaDetailCard
                subCri={subCri}
                key={subCri?._id}
                onRefetch={onRefetch}
                sgid={getQuery.state?.subCri?._id}
              />
            )
          )}

          {admissionSubGroupListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionSubGroupList?.list?.length === 0 && (
              <EmptyMessage
                title="No item found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            admissionSubGroupList?.list?.length === 0 && (
              <EmptyMessage
                title="No item."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddAdmissionSubjectCriteriaDetail
          onClose={onToggle}
          sgid={getQuery.state?.subCri?._id}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};
export default AdmissionSubjectCriteriaDetail;
